html,
body {
	height: 100%;
}

a {
	color: #007aff;
}

@media screen and (max-width: 40em) {
	.search-bar {
		width: 100%;
	}
}

.svg-shadow-2 {
	-webkit-filter: drop-shadow(6px 0 2px hsla(0, 0%, 0%, 0.2));
	filter: drop-shadow(6px 0 2px hsla(0, 0%, 0%, 0.2));
}

.max-h-100 {
	max-height: 100%;
}

.max-h-5-rows {
	max-height: 246px;
}

.min-w-3 {
	min-width: 4rem;
}

.mhn1 {
	margin-left: -0.5rem;
	margin-right: -0.5rem;
}

.mvn1 {
	margin-top: -0.25rem;
	margin-bottom: -0.25rem;
}

.mn05 {
	margin: -0.25rem;
}

.pv05 {
	padding-top: 0.125rem;
	padding-bottom: 0.125rem;
}

.on:focus {
	outline: none;
}

.circle {
	height: 0.75rem;
	width: 0.75rem;
	border-radius: 0.75rem;
}

.blue {
	color: #007aff !important;
}

.bg-map-beige {
	background-color: rgb(229, 227, 223);
}

.bg-orange {
	background-color: rgb(255, 149, 0) !important;
}

.mh-100 {
	max-height: 100%;
}

.max-lines-1 {
	overflow: hidden;
	white-space: nowrap;
	-o-text-overflow: ellipsis;
	   text-overflow: ellipsis;
}

.h-third {
	height: calc(100% / 3);
}

.h-two-thirds {
	height: calc(100% / 3 * 2);
}

.zoom-in {
	cursor: -webkit-zoom-in;
	cursor: zoom-in;
}



